import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import ReposList from 'src/components/ReposList';
import { useContainerRepos } from 'src/containers/Repos/hooks';
import { RepositoryFormModal } from 'src/components/RepositoryForm';
import Page from 'src/components/Page';
import { initialSelector } from 'src/containers/Repos/selectors';

export const ReposPage: React.FC = () => {
  useHooks();
  const isInitialLoading = useSelector(initialSelector);

  return (
    <Page isLoading={isInitialLoading}>
      <ReposList />
      <RepositoryFormModal />
    </Page>
  );
};

const useHooks = () => {
  const { fetchListRepoWatch, resetState } = useContainerRepos();
  useEffect(() => {
    return () => {
      resetState();
    };
  }, [resetState]);

  useEffect(() => {
    const req = fetchListRepoWatch();
    return () => {
      req.abort();
    };
  }, [fetchListRepoWatch]);
};
export default ReposPage;
